import React from "react"
import headshot from "../imgs/headshot.jpg"
function AboutPerson(props){
    return(
        <div className="about-content col-12 col-md-6 text-center small-bp">
            <img className="rounded-circle" width="50%" src={/*props.imgUrl*/headshot} alt={props.name}/>
            <h2 className="yugbsm">{props.name}</h2>
            <div className="col-8 text-center m-auto">
            <a href={props.twitter} target="_blank">
                <i className="fab fa-twitter fa-lg icon"></i>
            </a>
            <a href={props.instagram} target="_blank">
                <i className="fab fa-instagram fa-lg icon"></i>
            </a>
            <p>{props.bio}</p>
            </div>
        </div>
    )
}

export default AboutPerson