import React from "react"
import PodcastsContent from "./PodcastsContent"
import podcastData from "./podcastData"

import thumbnail from "../imgs/bridcast-thumbnail.jpg"
import thumbnail1 from "../imgs/bridcast-thumbnail.jpg"
import thumbnail2 from "../imgs/bridcast-thumbnail2.jpg"
import thumbnail3 from "../imgs/bridcast-thumbnail3.jpg"
import thumbnail4 from "../imgs/bridcast-thumbnail4.jpg"

class Podcasts extends React.Component{

    render(){
        const podcastItem = podcastData.map(item => <PodcastsContent key={item.id} imgUrl={/*item.imgUrl*/thumbnail} name={item.name} date={item.date} guestName={item.guestName}/>)

        return(
                <section id="podcasts">
                <div className="container-fluid">
                    <div className="row">
                        <h1 className="m-auto outbox">Bridcasts</h1>
                    </div>
                    {podcastItem}
                </div>
            </section>
        )
    }
}

export default Podcasts